import request from '../../utils/request'
import apiPath from '../url'
import qs from 'qs'
// 获取收藏页列表
export function getList1 (data) {
    data = qs.stringify(data)
    console.log(data)
    return request({
        url: apiPath.personal.release.getList1,
        method: 'post',
        data: data
    })
}

